body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  /* overflow-x: hidden; */
  background:	#fbd183 ;
}



/* Color Scheme */
header, footer, .chip-icon {
  background: #d06246!important;
}
.customCard, .customCard-front {
  background: #e3d8d8 !important;
}
.badge-secondary {
  background: #b0a1a1 !important;
}
.chip {
  background: #b0a1a1;
}
button.btn, button.bg-secondary{
  background: #d06246 !important;
  border: none;
}
button.bg-secondary:focus,
button.bg-secondary:hover,
button.btn-primary:focus,
button.btn-primary:hover {
  background-color: #d06246 !important;
  box-shadow: none;
}



#root {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
}
main {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

a:hover {
  text-decoration: none;
}

.navbar-brand {
  padding: 0;
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.customCard {
  padding: 2%;
  margin-top: 3%;
  border-radius: 5px;
}
.customCard-front {
  margin-top: 3%;
  border-radius: 5px;
}

.shadow {
  box-shadow: 1px 1px 10px 0px rgba(0,0,0,0.75) !important;
}

.imagePeople, .profileButtonSubmit{
  width: 100%;
}

/* input.form-control */
input {
  background-color: white !important;
  color: #000;
}
.form-control[value] {
  color: black !important;
  opacity: 1 !important;
}
.form-control::-webkit-input-placeholder { color: white; opacity: 0.5 }  /* WebKit, Blink, Edge */
.form-control:-moz-placeholder { color: white; opacity: 0.5 }  /* Mozilla Firefox 4 to 18 */
.form-control::-moz-placeholder { color: white; opacity: 0.5 }  /* Mozilla Firefox 19+ */
.form-control:-ms-input-placeholder { color: white; opacity: 0.5 }  /* Internet Explorer 10-11 */
.form-control::-ms-input-placeholder { color: white; opacity: 0.5 }  /* Microsoft Edge */

.form-control.modal-form[value] {
  color: #000 !important;
}
.form-control.modal-form::-webkit-input-placeholder { color: black; opacity: 0.5 }  /* WebKit, Blink, Edge */
.form-control.modal-form:-moz-placeholder { color: black; opacity: 0.5 }  /* Mozilla Firefox 4 to 18 */
.form-control.modal-form::-moz-placeholder { color: black; opacity: 0.5 }  /* Mozilla Firefox 19+ */
.form-control.modal-form:-ms-input-placeholder { color: black; opacity: 0.5 }  /* Internet Explorer 10-11 */
.form-control.modal-form::-ms-input-placeholder { color: black; opacity: 0.5 }  /* Microsoft Edge */


.inputimage, .inputname, .inputSurname, .inputInfo{
  width: 100%;
  margin-bottom: 10px;
}
.inputLine{
  display: inline-block;
  margin-right: 1%;
  margin-bottom: 2%;
  border-left: none;
  border-top: none;
  border-right:none;
}
.profileheight{
  height: 250px;
}
.thumb {
  display: inline-block;
  width: 200px;
  height: 200px;
  margin: 5px;
  background-position: center center;
  background-size: cover;
}

.videoWrap {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  margin-bottom: 10px;
}

iframe {
  position: absolute;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  border: none;
}

.form-control:focus{
  border-color: none;
  box-shadow: none;
  outline: none
}
.btn-primary:focus {
  border-color: none;
  box-shadow: none;
  outline: none
}

.dropdown-item:hover, .trashcan:hover, .page-item:hover {
  cursor: pointer;
}

.trashcan:hover {
  color: red;
}


.chip {
  display: inline-block;
  padding: 0 12px;
  border-radius: 32px;
  font-size: 13px;
}
.chip, .chip-icon {
  height: 32px;
  line-height: 32px;
}
.chip-icon {
  display: block;
  float: left;
  width: 32px;
  border-radius: 50%;
  text-align: center;
  color: white;
  margin: 0 8px 0 -12px;
}
.chips {
  padding: 12px 0;
}

.dropdown-toggle::after {
  display: none;
}
.videoButton {
  text-align: center;
}

h3 {
  color: #555;
}

#presentation {
  width: 480px;
  height: 120px;
  padding: 20px;
  margin: auto;
  background: #fff;
  margin-top: 10px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  transition: all 0.3s;
  border-radius: 3px;
}

#presentation:hover {
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  transition: all 0.3s;
  -webkit-transform: translateZ(10px);
          transform: translateZ(10px);
}

#floating-button {
  width: 55px;
  height: 55px;
  border-radius: 50%;
  background: #44414f;
  position: fixed;
  bottom: 30px;
  right: 30px;
  cursor: pointer;
  box-shadow: 0px 2px 5px #666;
}
.textpost {
  position: relative;
  z-index: 11;
  top: center;
  left: center;
}

.plus {
  color: white;
  position: absolute;
  top: 0;
  display: block;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  padding: 0;
  margin: 0;
  line-height: 55px;
  font-size: 38px;
  font-family: "Roboto";
  font-weight: 300;
  -webkit-animation: plus-out 0.3s;
          animation: plus-out 0.3s;
  transition: all 0.3s;
}

#container-floating {
  position: fixed;
  width: 70px;
  height: 70px;
  bottom: 30px;
  right: 30px;
  z-index: 50px;
}

#container-floating:hover {
  height: 400px;
  width: 90px;
  padding: 30px;
}

#container-floating:hover .plus {
  -webkit-animation: plus-in 0.15s linear;
          animation: plus-in 0.15s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.edit {
  position: absolute;
  top: 0;
  display: block;
  bottom: 0;
  left: 0;
  display: block;
  right: 0;
  padding: 0;
  opacity: 0;
  margin: auto;
  line-height: 65px;
  -webkit-transform: rotateZ(-70deg);
          transform: rotateZ(-70deg);
  transition: all 0.3s;
  -webkit-animation: edit-out 0.3s;
          animation: edit-out 0.3s;
}

#container-floating:hover .edit {
  -webkit-animation: edit-in 0.2s;
          animation: edit-in 0.2s;
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes edit-in {
  from {
    opacity: 0;
    -webkit-transform: rotateZ(-70deg);
            transform: rotateZ(-70deg);
  }
  to {
    opacity: 1;
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
}

@keyframes edit-in {
  from {
    opacity: 0;
    -webkit-transform: rotateZ(-70deg);
            transform: rotateZ(-70deg);
  }
  to {
    opacity: 1;
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
}

@-webkit-keyframes edit-out {
  from {
    opacity: 1;
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  to {
    opacity: 0;
    -webkit-transform: rotateZ(-70deg);
            transform: rotateZ(-70deg);
  }
}

@keyframes edit-out {
  from {
    opacity: 1;
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  to {
    opacity: 0;
    -webkit-transform: rotateZ(-70deg);
            transform: rotateZ(-70deg);
  }
}

@-webkit-keyframes plus-in {
  from {
    opacity: 1;
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  to {
    opacity: 0;
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
  }
}

@keyframes plus-in {
  from {
    opacity: 1;
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  to {
    opacity: 0;
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
  }
}

@-webkit-keyframes plus-out {
  from {
    opacity: 0;
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
  }
  to {
    opacity: 1;
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
}

@keyframes plus-out {
  from {
    opacity: 0;
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
  }
  to {
    opacity: 1;
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
}

.nds {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: fixed;
  z-index: 300;
  -webkit-transform: scale(0);
          transform: scale(0);
  cursor: pointer;
}

.nd1 {
  background: #d3a411;
  right: 40px;
  bottom: 120px;
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
  -webkit-animation: bounce-out-nds 0.3s linear;
          animation: bounce-out-nds 0.3s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.nd3 {
  background: #3c80f6;
  right: 40px;
  bottom: 180px;
  -webkit-animation-delay: 0.15s;
          animation-delay: 0.15s;
  -webkit-animation: bounce-out-nds 0.15s linear;
          animation: bounce-out-nds 0.15s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.nd4 {
  background: #ba68c8;
  right: 40px;
  bottom: 240px;
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
  -webkit-animation: bounce-out-nds 0.1s linear;
          animation: bounce-out-nds 0.1s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.nd5 {
  background-image: url("https://lh3.googleusercontent.com/-X-aQXHatDQY/Uy86XLOyEdI/AAAAAAAAAF0/TBEZvkCnLVE/w140-h140-p/fb3a11ae-1fb4-4c31-b2b9-bf0cfa835c27");
  background-size: 100%;
  right: 40px;
  bottom: 300px;
  -webkit-animation-delay: 0.08s;
          animation-delay: 0.08s;
  -webkit-animation: bounce-out-nds 0.1s linear;
          animation: bounce-out-nds 0.1s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes bounce-nds {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes bounce-nds {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes bounce-out-nds {
  from {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

@keyframes bounce-out-nds {
  from {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

#container-floating:hover .nds {
  -webkit-animation: bounce-nds 0.1s linear;
          animation: bounce-nds 0.1s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

#container-floating:hover .nd3 {
  -webkit-animation-delay: 0.08s;
          animation-delay: 0.08s;
}
#container-floating:hover .nd4 {
  -webkit-animation-delay: 0.15s;
          animation-delay: 0.15s;
}
#container-floating:hover .nd5 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

